import React, { useState, useContext, useEffect, useRef, useMemo } from "react";
import "./Task2.css";
import { Context } from "../../Context";
import { HeadingText } from "../HeadingText/HeadingText";
import { weighers, lines } from "./img";

const { weigher1, weigher2, weigher3 } = weighers;
const { lines1, lines2, lines3 } = lines;

const weighersImages = [
  {
    id: 1,
    name: "weigher1",
    src: weigher1,
    tag: "1",
  },
  {
    id: 2,
    name: "weigher2",
    src: weigher2,
    tag: "2",
  },
  {
    id: 3,
    name: "weigher3",
    src: weigher3,
    tag: "3",
  },
];

const linesImages = [
  {
    id: 1,
    name: "lines1",
    src: lines1,
    tag: "1",
  },
  {
    id: 2,
    name: "lines2",
    src: lines2,
    tag: "2",
  },
  {
    id: 3,
    name: "lines3",
    src: lines3,
    tag: "3",
  },
];

// Контент модального окна - Алфавит
const AlphabetModalContent = () => {
  return (
    <>
      <h3> Словать</h3>
      <br />
      <p>Много разных слов</p>
    </>
  );
};

// Контент модального окна - Помощь
const HelpModalContent = () => {
  return (
    <>
      <h3>Электронный помощник</h3>
      <br />
      <p>Контент направленный на помощь в обучении</p>
    </>
  );
};

// Изменение заголовка (поддерживаеи html тэги)
// START
// Верхний заголовок - название урока ( <> текст размещать внутри тэгов </> )
const LessonHeading = () => (
  <>
    <p>Схемы и рисунки</p>
  </>
);
// Заголовок с заданием к уроку ( <> текст размещать внутри тэгов </> )
const ThemeHeading = () => (
  <>
    <p style={{ textAlign: "center" }}>
      Какая схема подходит к каждому рисунку? Соедини их.
    </p>
  </>
);
// Текс title с переводом (НЕ поддерживает html тэги)
const titleHeading = "First slide";

function Task2() {
  const {
    helpSetIcon,
    alphabetSetIcon,
    helpSetModalContent,
    alphabetSetModalContent,
    lessonSetStartAction,
    lessonSetResetTask,
    resetTask,
    lessonSetCheckTask,
    checkTask,
    lessonSetAndAnswer,
    lessonSetViewFeedback
  } = useContext(Context);

  const [lines, setLines] = useState(linesImages);
  const dragPlace = useRef(null);
  const dropPlace = useRef(null);
  console.log('render')

  // Иконки правого поля  - (помощь и алфавит)
  useEffect(() => {
    //  helpSetIcon - иконка электронного помощника (включить - true, отключить - false)
    helpSetIcon(true);
    //  alphabetSetIcon - иконка алфавита (включить - true, отключить - false)
    alphabetSetIcon(true);
    helpSetModalContent(<HelpModalContent />);
    alphabetSetModalContent(<AlphabetModalContent />);
    setLines((prev) => prev.sort(() => Math.random() - 0.5))
  }, []);

  useEffect(() => {
    if (resetTask === true) {
      dropPlace.current.childNodes.forEach((item) => {
        if (item.childNodes.length) {
          let draggingItem = item.childNodes[0];
          draggingItem.style.position = "relative";
          draggingItem.style.backgroundColor = "aliceblue";
          draggingItem.style.zIndex = null;
          draggingItem.style.top = null;
          draggingItem.style.left = null;
          dragPlace.current.append(draggingItem);
        }
      });
      setLines((prev) => prev.sort(() => Math.random() - 0.5))
      lessonSetResetTask(false);
    }
  }, [resetTask]);

  useEffect(() => {
    if (checkTask === true) {
      let winVar = 0;
      dropPlace.current.childNodes.forEach((item) => {
        if (
          item.childNodes.length === 1 &&
          item.childNodes[0].getAttribute("drag-data") ===
            item.getAttribute("drop-data")
        ) {
          winVar += 1;
        }
      });
      if (winVar === linesImages.length) {
        lessonSetAndAnswer(true);
      } else lessonSetAndAnswer(false);
    }
    lessonSetCheckTask(false);
  }, [checkTask]);

  let draggingItem;
  let elemBelow;
  function mouseDown(event) {
    lessonSetViewFeedback(false)
    draggingItem = event.target;
    draggingItem.style.cursor = "grabbing";
    let shiftX = event.clientX - draggingItem.getBoundingClientRect().left;
    let shiftY = event.clientY - draggingItem.getBoundingClientRect().top;

    draggingItem.style.position = "absolute";
    draggingItem.style.zIndex = 1000;
    document.body.append(draggingItem);

    moveAt(event.pageX, event.pageY);

    function moveAt(pageX, pageY) {
      draggingItem.style.left = pageX - shiftX + "px";
      draggingItem.style.top = pageY - shiftY + "px";
    }

    elemBelow = document.elementFromPoint(event.clientX, event.clientY);

    function onMouseMove(event) {
      moveAt(event.pageX, event.pageY);

      draggingItem.hidden = true;
      elemBelow = document.elementFromPoint(event.clientX, event.clientY);
      draggingItem.hidden = false;

      if (!elemBelow) return;
    }
    document.addEventListener("mousemove", onMouseMove);

    draggingItem.onmouseup = function () {
      document.removeEventListener("mousemove", onMouseMove);
      draggingItem.style.cursor = "grab";

      if (elemBelow.classList.contains("dropPlace_weighers") && !elemBelow.childNodes.length) {
        draggingItem.style.bottm = "relative";
        draggingItem.style.backgroundColor = "lightblue";
        draggingItem.style.zIndex = null;
        draggingItem.style.top = null;
        draggingItem.style.left = null;
        elemBelow.append(draggingItem);

        lessonSetStartAction(true);
      } else {
        draggingItem.style.position = "relative";
        draggingItem.style.backgroundColor = "aliceblue";
        draggingItem.style.zIndex = null;
        draggingItem.style.top = null;
        draggingItem.style.left = null;
        dragPlace.current.append(draggingItem);
      }
    };
  }
  //  Внесение изменений в контент задания
  return (
    <div className="lessonContent">
      <HeadingText
        lessonHeading={<LessonHeading />}
        themeHeading={<ThemeHeading />}
        titleHeading={titleHeading}
        // soundSource={soundSource}
        soundVisible={true}
        titleVisible={true}
      />
      <div className="slideWrapper">
        <div className="dropPlaceWrapper_weighers" ref={dropPlace}>
          {weighersImages.map((item) => (
            <div
              className="dropPlace_weighers"
              key={item.id}
              drop-data={item.tag}
              style={{ backgroundImage: `url(${item.src})` }}
            ></div>
          ))}
        </div>
        <div className="dragPlaceWrapper_lines" ref={dragPlace}>
          {lines.map((item) => (
            <div
              className="dragPlace_lines"
              key={item.id}
              onMouseDown={mouseDown}
              style={{ backgroundImage: `url(${item.src})` }}
              drag-data={item.tag}
            ></div>
          ))}
        </div>
      </div>
    </div>
  );
}

export default Task2;
