import React, { useState, useContext, useEffect, useRef, useMemo } from "react";
import "./Task3.css";
import { Context } from "../../Context";
import { HeadingText } from "../HeadingText/HeadingText";
import squares from './img/squares.png'


// Контент модального окна - Алфавит
const AlphabetModalContent = () => {
  return (
    <>
      <h3> Словать</h3>
      <br />
      <p>Много разных слов</p>
    </>
  );
};

// Контент модального окна - Помощь
const HelpModalContent = () => {
  return (
    <>
      <h3>Электронный помощник</h3>
      <br />
      <p>Контент направленный на помощь в обучении</p>
    </>
  );
};

// Изменение заголовка (поддерживаеи html тэги)
// START
// Верхний заголовок - название урока ( <> текст размещать внутри тэгов </> )
const LessonHeading = () => (
  <>
    <p>Математика</p>
  </>
);
// Заголовок с заданием к уроку ( <> текст размещать внутри тэгов </> )
const ThemeHeading = () => (
  <>
    <p style={{ textAlign: "center" }}>
    Сколько четырехугольников на рисунке?
    </p>
  </>
);
// Текс title с переводом (НЕ поддерживает html тэги)
const titleHeading = "First slide";

//варианты ответов
const numbers = [
  { id: 1, data: "4" },
  { id: 2, data: "5" },
  { id: 3, data: "7" },
  { id: 4, data: "9" },
];

//вариант ответа
const Number = ({ number, answer }) => {
  const [border, setBorder] = useState(false);

  useEffect(() => {
    if (answer === number) {
      setBorder(true);
    } else setBorder(false);
  }, [answer]);

  return (
    <div
      className="number"
      style={border ? { border: "8px solid #6aa84f" } : null}
      data-number={number}
    >
      {number}
    </div>
  );
};

function Task3() {
  const {
    helpSetIcon,
    alphabetSetIcon,
    helpSetModalContent,
    alphabetSetModalContent,
    lessonSetStartAction,
    lessonSetResetTask,
    resetTask,
    lessonSetCheckTask,
    checkTask,
    lessonSetAndAnswer,
    lessonSetViewFeedback,
  } = useContext(Context);

  const [answers, setAnswers] = useState(numbers);
  const [answer, setAnswer] = useState(null);
  const [winVar] = useState("9");

  // Иконки правого поля  - (помощь и алфавит)
  useEffect(() => {
    console.log("mount");
    //  helpSetIcon - иконка электронного помощника (включить - true, отключить - false)
    helpSetIcon(true);
    //  alphabetSetIcon - иконка алфавита (включить - true, отключить - false)
    alphabetSetIcon(true);
    helpSetModalContent(<HelpModalContent />);
    alphabetSetModalContent(<AlphabetModalContent />);
    setAnswers(numbers.sort(() => Math.random() - 0.5));
  }, []);

  useEffect(() => {
    if (resetTask) {
      setAnswer(null);
      answers.forEach((item) => (item.border = false));
      setAnswers((prev) => prev.sort(() => Math.random() - 0.5));
      lessonSetResetTask(false);
      console.log("reset");
    }
  }, [resetTask]);

  useEffect(() => {
    if (checkTask === true) {
      if (answer === winVar) {
        lessonSetAndAnswer(true);
      } else lessonSetAndAnswer(false);
    }
    lessonSetCheckTask(false);
  }, [checkTask]);


  function handleClick(e) {
    if (e.target.className === "number") {
      setAnswer(e.target.getAttribute("data-number"));
      lessonSetStartAction(true);
      lessonSetViewFeedback(false);
    }
  }

  //  Внесение изменений в контент задания
  return (
    <div className="lessonContent">
      <HeadingText
        lessonHeading={<LessonHeading />}
        themeHeading={<ThemeHeading />}
        titleHeading={titleHeading}
        // soundSource={soundSource}
        soundVisible={true}
        titleVisible={true}
      />
      <div className="slideWrapper">
        <div className="imgWrapper">
          <img src={squares} alt="toys" />
        </div>
        <div className="answers" onClick={handleClick}>
          {answers.map((item) => (
            <Number key={item.id} number={item.data} answer={answer} />
          ))}
        </div>
      </div>
    </div>
  );
}

export default Task3;
